/* Root variables */
:root {
    --font-family-title: Newsreader, Raleway, Helvetica Neue, Helvetica, Arial, sans-serif;
    --font-family-base: 'Inter', Open Sans, Helvetica Neue, Helvetica, Arial, sans-serif;

    --font-size-large: clamp(20px, 2vw + 1rem, 70px);
    --font-size-medium: clamp(14px, 2vw + .2rem, 17px);
    --font-size-small: clamp(12px, 2vw + .2rem, 22px);


    --font-weight-regular: 400;
    --font-weight-bold: 700;


    --primary-green: #426B1F;
    --primary-black: #000000;
    --primary-white: #FFFFFF;  

    --border-color: #E6E6E6;
}

body {
    font-family: var(--font-family-base);
    font-size: var(--font-size-medium);
    line-height: 1.8;
}

.login {
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;;
    margin-bottom: 50px;
}

.login-container{
    text-align: center;
    width: clamp(280px, 85%, 600px);
    margin-top: 50px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
}

.login-container h2, .login-header {
    margin-top: 50px;
    font-family: var(--font-family-title);
    font-weight: var(--font-weight-bold);
    font-size: var(--font-size-large);
}

.login-container .login-header {
    font-weight: var(--font-weight-regular);
}

.login-container .input-container {
    display: grid;
    margin-left: 15px;
    line-height: 3;
}

.login-container input {
    margin: auto;
    width: 70%;
    height: 35px;
    padding-left: 10px;
    border: 1px solid var(--primary-black);
    border-radius: 10px;
}

.login-error-message {
    line-height: 1.2;
    width: 70%;
    text-align: center;
    color: var(--primary-red);
    font-size: var(--font-size-extra-small);
}

.login-container label {
    font-weight: var(--font-weight-bold);
} 

/* For the Sign Up link */
.green {
    color: var(--primary-green);
    text-decoration: underline;
}

.small {
    font-size: var(--font-size-small);
}

/* Login button */
.continue {
    width: clamp(80px, 27%, 150px);
    height: clamp(20px, 10vw, 40px);
    color: var(--primary-white);
    background-color: var(--primary-black);
    border: none;
    border-radius: 5px;
    margin: 30px auto;
    font-size: var(--font-size-medium);
}

.continue:hover {
    font-weight: var(--font-weight-bold);
    transition: .3s ease;
    height: clamp(25px, 10vw, 45px);
}